// Imports

import * as React from "react";

import Container from "@mui/material/Container";
import Slider from "@mui/material/Slider";
import Typography from "@mui/material/Typography";
import FormControlLabel from "@mui/material/FormControlLabel";
import Switch from "@mui/material/Switch";
import Button from "@mui/material/Button";
import Snackbar from "@mui/material/Snackbar";
import Alert from "@mui/material/Alert";

// CSS Files

import "../css/animatedbackground.scss";

import "../css/xtrasmall.css";
import "../css/small.css";
import "../css/medium.css";
import "../css/large.css";
import "../css/xtralarge.css";

// Markup

const IndexPage = () => {

  const [NumberOfCharacters, SetNumberOfCharacters] = React.useState(10);
  const [IsOpen, SetOpen] = React.useState(false);
  const [IsError, SetError] = React.useState(false);
  const [CooldownActive, SetCooldown] = React.useState(false);
  var lowerAlphabet = "abcdefghijklmnopqrstuvwxyz".split("");
  var upperAlphabet = "ABCDEFGHIJKLMNOPQRSTUVWXYZ".split("");
  var numbers = "1234567890".split("");
  var punctuation = "!$%^&*()<,>.?/:;@'~#{[}]".split("");

  const HandleChange = (event) => {

    SetNumberOfCharacters(event.target.value);

  }

  const HandleClose = (event, reason) => {

    if(reason === "clickaway"){

      return;

    }

    SetOpen(false);
    
  }

  const HandleErrorClose = (event, reason) => {

    if(reason === "clickaway"){

      return;

    }

    SetError(false);

  }

  const GeneratePassword = () => {

    if(CooldownActive === true) return;

    var PasswordElementLabel = document.getElementById("PasswordTypographyLabel");
    var GeneratedPasswordElementLabel = document.getElementById("GeneratedPasswordTypographyLabel");

    var NoOfChars = NumberOfCharacters;
    var LettersSwitch = document.getElementById("LettersSwitch").checked;
    var MixedCaseSwitch = document.getElementById("MixedCaseSwitch").checked;
    var NumbersSwitch = document.getElementById("NumbersSwitch").checked;
    var PunctuationSwitch = document.getElementById("PunctuationSwitch").checked;

    if(LettersSwitch === false && MixedCaseSwitch === false && NumbersSwitch === false && PunctuationSwitch === false){

      SetError(true);

      SetCooldown(true);

      setTimeout(function(){

        SetCooldown(false);

      }, 5000);

      return;

    }

    var UsableCharacters = [];

    if(LettersSwitch === true){

      lowerAlphabet.map(item => UsableCharacters.push(item));

    }

    if(MixedCaseSwitch === true){

      upperAlphabet.map(item => UsableCharacters.push(item));

    }

    if(NumbersSwitch === true){

      numbers.map(item => UsableCharacters.push(item));

    }

    if(PunctuationSwitch === true){

      punctuation.map(item => UsableCharacters.push(item));

    }

    var password = "";

    for(var i = 0; i < NoOfChars; i++){

      var RandomCharacter = UsableCharacters[Math.floor(Math.random() * ((UsableCharacters.length - 1)))];
      password += RandomCharacter;

    }

    PasswordElementLabel.innerHTML = "Your generated password is:";
    GeneratedPasswordElementLabel.innerHTML = password;

    SetOpen(true);
    SetCooldown(true);

    setTimeout(function(){

      SetCooldown(false);

    }, 5000);

  }

  return (

    <>

      <div class="stars">
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
        <div class="star"></div>
      </div>

      <meta name="viewport" content="width=device-width, initial-scale=1.0" />
    
      <title>Password Generator</title>

      <main>

        <Typography id="Title" variant="h3">Password Generator</Typography>

        <br/><br/>


        <Container id="SliderContainer" maxWidth="xs">

          <Typography id="NumOfCharactersLabel" align="center">Number Of Characters</Typography>
          <Slider id="NumOfCharactersSlider" onChange={HandleChange} defaultValue={10} aria-label="Default" valueLabelDisplay="auto"/>

        </Container>

        <br/>

        <Container id="SwitchContainer">

          <FormControlLabel id="LettersLabel" control={<Switch id="LettersSwitch" defaultChecked />} label="Letters" />

          <div className="breakSwitch"></div>

          <FormControlLabel id="MixedCaseLabel" control={<Switch id="MixedCaseSwitch" defaultChecked />} label="Mixed Case" />

          <div className="breakSwitch"></div>

          <FormControlLabel id="NumbersLabel" control={<Switch id="NumbersSwitch" defaultChecked />} label="Numbers" />

          <div className="breakSwitch"></div>

          <FormControlLabel id="PunctuationLabel" control={<Switch id="PunctuationSwitch" defaultChecked />} label="Punctuation" />
          
        </Container>

        <br/>

        <Button id="GenerateButton" onClick={GeneratePassword} variant="contained">Generate</Button>

        <br/>

        <div id="GeneratedPasswordDiv">

          <Typography id="PasswordTypographyLabel" variant="h6">Your generated password will be shown here.</Typography>
          <Typography id="GeneratedPasswordTypographyLabel" variant="h6">{" "}</Typography>

        </div>








        <Snackbar open={IsOpen} autoHideDuration={5000} onClose={HandleClose}>

          <Alert onClose={HandleClose} severity="success" sx={{ width: "100%" }}>Your password has been generated!</Alert>

        </Snackbar>

        <Snackbar open={IsError} autoHideDuration={5000} onClose={HandleErrorClose}>

          <Alert onClose={HandleErrorClose} severity="warning" sx={{ width: "100%" }}>You have not selected any character sets.</Alert>

        </Snackbar>



      </main>
    
    </>

  )
}

export default IndexPage